import React from 'react';
import styled from 'styled-components';
import locationIcon from '../images/locationIcon.png';
import { countries } from '../misc/countryCodes';
import { useCountry } from '../context/TopHeadlinesContextProvider';

const StyledCountrySelector = styled.div`
  height: 2.2rem;
  width: 2.2rem;
  background-image: ${props => {
    if (props.countryCode)
      return `url('https://newsapi.org/images/flags/${props.countryCode}.svg')`;
    return `url(${locationIcon})`;
  }};

  background-size: contain;
  background-repeat: no-repeat;
  margin: 0.5rem;
  position: relative;
  display: flex;
  justify-content: center;
  border-radius: 0.4rem;
  cursor: pointer;

  i {
    position: absolute;
    bottom: -1rem;
    width: 2.2rem;
    text-align: center;
  }

  &:hover .dropdown-menu {
    display: block;
  }

  .dropdown-menu {
    display: none;
    position: absolute;
    top: 3rem;
    background-color: #ffffff;
    border-radius: 0.4rem;
    box-shadow: 0px 0px 10px gray;
    padding: 0.5rem;
    width: 15rem;
    overflow-y: scroll;
    z-index: 3;
  }

  .option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.2rem;
    border-bottom: 1px solid gray;
    padding-bottom: 0.5rem;
    margin-bottom: 0.5rem;
    font-weight: 500;
    &:hover {
      color: #ef5350;
    }
  }
`;

const Flag = styled.div`
  height: 2.2rem;
  width: 2.2rem;
  background-image: ${props => {
    if (props.countryCode)
      return `url('https://newsapi.org/images/flags/${props.countryCode}.svg')`;
    return `url(${locationIcon})`;
  }};
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 0.4rem;
`;

const CountrySelector = () => {
  const { currentCountryIndex, updateCountryIndex } = useCountry();

  return (
    <StyledCountrySelector
      countryCode={countries[currentCountryIndex].countryCode}
    >
      <i className="fas fa-chevron-down" />
      <div className="dropdown-menu">
        {countries.map((countryData, index) => {
          return (
            // eslint-disable-next-line
            <div
              className="option"
              key={index}
              onClick={() => {
                updateCountryIndex(index);
              }}
            >
              <Flag countryCode={countryData.countryCode} />
              {countryData.countryName}
            </div>
          );
        })}
      </div>
    </StyledCountrySelector>
  );
};

export default CountrySelector;
