import React from 'react';
import { Switch, Route } from 'react-router-dom';
import TopHeadlinesContextProvider from './context/TopHeadlinesContextProvider';
import HomePage from './pages/HomePage';

const App = () => {
  return (
    <Switch>
      <TopHeadlinesContextProvider>
        <Route exact path="/">
          <HomePage />
        </Route>
      </TopHeadlinesContextProvider>

      <Route>Page Does Not Exists</Route>
    </Switch>
  );
};

export default App;
