import React, { useContext } from 'react';
import CountryProvider, { countryContext } from './CountryProvider';
import CategoryProvider, { categoryContext } from './CategoryProvider';

const TopHeadlinesContextProvider = ({ children }) => {
  return (
    <CountryProvider>
      <CategoryProvider>{children}</CategoryProvider>
    </CountryProvider>
  );
};

export default TopHeadlinesContextProvider;

export const useCountry = () => useContext(countryContext);
export const useCategory = () => useContext(categoryContext);
