/* eslint-disable */
import React, { useRef } from 'react';
import Card from './Card';

const CardGrid = ({ apiResult, isLoading }) => {
  const cardGridRef = useRef();

  const scrollToTop = () => {
    if (cardGridRef.current) cardGridRef.current.scrollTo(0, 0);
  };
  return (
    <>
      <div className="card-grid" ref={cardGridRef}>
        {isLoading && (
          <div className="loader">
            <div className="loader-img" />
          </div>
        )}
        {!isLoading &&
          apiResult &&
          apiResult.map((article, index) => {
            return (
              <Card
                key={index}
                title={article.title}
                image={article.urlToImage}
                url={article.url}
              />
            );
          })}
      </div>
      <div className="scroll-to-top" onClick={scrollToTop}>
        <i className="fas fa-arrow-up"></i>
      </div>
    </>
  );
};

export default CardGrid;
