import React from 'react';
import styled from 'styled-components';
import noImage from '../images/noImage.png';
import { Button3 } from '../styledComponents/Buttons';

const StyledCard = styled.div`
  height: 100%;
  width: 100%;
  border-radius: 0.5rem;
  background-repeat: no-repeat;
  padding: 0.5rem;
  background-image: linear-gradient(
      to bottom,
      rgba(33, 33, 33, 0.1872126547138343) 10%,
      rgba(33, 33, 33, 0.7911452389834972) 75%
    ),
    ${props => `url(${props.image ? props.image : noImage})`};
  color: white;
  background-size: ${props => (props.image ? 'cover' : 'contain')};
  background-position: center top;
  position: relative;
  font-size: 1.2rem;
  .description {
    position: absolute;
    min-width: 90%;
    margin: 0.5rem;
    margin-bottom: 0.7rem;
    bottom: 0;
  }

  .button-container {
    display: flex;
    width: 100%;
    margin-top: 0.5rem;
    padding-right: 1rem;
    justify-content: right;

    a {
      text-decoration: none;
      color: black;
    }
  }

  .description-text {
    padding: 0.5rem;
    transition: all 0.5s linear;
  }

  &:hover .description-text {
    transform: scale(1.05);
  }

  @media screen and (min-width: 925px) {
    &:first-child {
      grid-column: 1/3;
    }
  }

  @media screen and (max-width: 350px) {
    font-size: 1rem;
  }
`;

const Card = ({ title, image, url }) => {
  return (
    <StyledCard image={image}>
      <div className="description">
        <div className="description-text">{title}</div>
        <div className="button-container">
          <a href={url} target="_blank" rel="noreferrer">
            <Button3>Read</Button3>
          </a>
        </div>
      </div>
    </StyledCard>
  );
};

export default Card;
