import React, { useRef, useState } from 'react';
import { Button2 } from '../styledComponents/Buttons';
import { categories } from '../misc/categories';
import { useCategory } from '../context/TopHeadlinesContextProvider';

const Sidebar = () => {
  const modal = useRef();
  const sidebarRef = useRef();
  const { currentCategory, updateCategory } = useCategory();

  const [modalActive, setModalActive] = useState(false);
  const toggleModal = () => {
    if (modalActive) {
      modal.current.classList.remove('open-modal');
      setModalActive(false);
    } else {
      modal.current.classList.add('open-modal');
      setModalActive(true);
    }
  };

  const toggleSidebar = () => {
    if (sidebarRef.current.classList.contains('sidebar-active')) {
      sidebarRef.current.classList.remove('sidebar-active');
    } else {
      sidebarRef.current.classList.add('sidebar-active');
    }
  };
  /* eslint-disable*/
  return (
    <>
      <div className="open-sidebar-button" onClick={toggleSidebar}>
        <i className="fas fa-bars"></i>
      </div>
      <div className="sidebar" ref={sidebarRef}>
        <div className="close-sidebar-button">
          <i className="fas fa-times" onClick={toggleSidebar} />
        </div>
        <div className="categories-heading">Categories</div>
        {categories.map((category, index) => {
          return (
            <Button2
              key={index}
              onClick={() => {
                updateCategory(category);
                if (window.innerWidth <= 700) {
                  toggleSidebar();
                }
              }}
              active={category === currentCategory}
            >
              {category}
            </Button2>
          );
        })}

        <button type="button" className="about-button" onClick={toggleModal}>
          About Us
          <i className="fas fa-question" />
        </button>
      </div>
      <div className="modal-container" ref={modal}>
        <div className="modal-window">
          <div className="modal-heading">
            <span>About Us</span>
            <i className="fas fa-times" onClick={toggleModal} />
          </div>
          <div className="modal-information">
            This web app displays news using a free api from{' '}
            <a href="https://newsapi.org">newsapi.org</a>. The app is built
            using React Js library and has various features you can play with.
            This website is made for educational purposes and does not aim to
            earn profit by using the content provided from any of the sources
            used.
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
