import React from 'react';

import Logo from './Logo';

import GetDate from './GetDate';
import CountrySelector from './CountrySelector';

const Header = () => {
  return (
    <div className="header">
      <Logo />

      <div className="date-country">
        <CountrySelector />
        <GetDate />
      </div>
    </div>
  );
};

export default Header;
